body {
  padding: 0;
  margin: 0;
}
.titletext {
  font-size: x-large;
  text-decoration: none;
  color: #3a3b3c;
  cursor: pointer;
  padding-left: 20px;
}
.smalltext {
  color: rgba(0, 0, 0, 0.5);
  font-family: "Raleway";
  // font-style: bold;
  text-align: left;
  cursor: pointer;
  padding-left: 20px;
  padding-bottom: 20px;
}

a {
  text-decoration: none;
  color: var(--primary-blue);
}

.split-screen {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  background-color: #f4f7f6;
  padding-top: 10%;
  padding-bottom: 10%;
  row-gap: 30px;
}

.hleft,
.hright {
  display: flex;
  justify-content: center;
  align-items: center;
  // width: 50%;
}

.card {
  // border-radius: 30px;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
  text-decoration: none;
}

// .card {
//   width: 400px;
// }
.casepic {
  max-width: 500px;
  cursor: pointer;
}

